: 12px;\n\n    outline: none;\n    border: none;\n    border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n    \n    color: $input-select-text;\n\n    padding: 16px 24px;\n\n    position: relative;\n\n    resize: none;\n\n    &::placeholder{\n      color:$input-placeholder-text\n    }\n  }\n  \n}",".services-list {\n  display: grid;\n  grid-template-columns: 1fr;\n  grid-gap: 44px;\n  align-items: center;\n}\n\n.services-item:not(:last-child) {\n  margin-bottom: 44px;\n}\n\n\n@media screen and (min-width: 1200px) {\n  .services-list {\n    grid-gap: 68px;\n  }\n  .services-item:nth-child(odd) {\n    display: flex;\n    flex-direction: row-reverse;\n    gap: clamp(24px, 2vw, 48px);\n  }\n  .services-item:nth-child(even) {\n    display: flex;\n    flex-direction: row;\n    gap: clamp(24px, 2vw, 48px);\n  }\n  .service-descr {\n      width: clamp(350px, 100%, 567px);\n  }\n\n  .services-btn {\n      border: $btn-secondary-accent-border;\n      color: $btn-secondary-desktop-text;\n      width: 200px;\n      margin-top: 44px;\n      transition: $transition;\n  }\n  .services-btn:hover, .services-btn:focus {\n      background: $btn-secondary-accent-bg;\n      color: $btn-secondary-accent-text;\n      \n  }\n\n  .service-title {\n    margin-top: 0;\n  }\n  .service-descr {\n    background-color: $card-bg;\n    border-radius: 24px;\n    padding-top: 32px;\n    padding-bottom: 32px;\n    padding-left: clamp(12px, -0.4rem + 5vw, 56px);\n    padding-right: 32px\n  }\n  .services-img {\n    width: 777px;\n  }\n}\n\n.services-img {\n  width: clamp(21.875rem, -4.688rem + 106.25vw, 75rem);\n}\n\n\n\n.services-btn {\n  margin-top: 32px;\n  border: $btn-secondary-border;\n  border-radius: 12px;\n  padding: 16px 24px;\n  font-weight: 500;\n  font-size: 18px;\n  text-align: center;\n  cursor: pointer;\n}\n\n.services-btn:hover, .services-btn:focus {\n    background: $white-text;\n    color: $btn-primary-disable-bg;\n    transition: $transition;\n}\n.service-title {\n  margin-top: 2